














































































































































































import CompanyInterface from '@/interface/company.inteface';
import DepartmentInterface from '@/interface/department.interface';
import DivisionInterface from '@/interface/division.interface';
import SubdivisionInterface from '@/interface/subdivision.interface';
import UserInterface from '@/interface/user.interface';
import UserServices from '@/services/users.service';
import { VForm } from '@/utils/types';
import axios from 'axios';
import { get } from 'lodash';
import Vue from 'vue';
import Component from 'vue-class-component';
import { Ref } from 'vue-property-decorator';
import { namespace } from 'vuex-class';
const MainModule = namespace('MainModule');

@Component
export default class UpdateUserPage extends Vue {
  @Ref('form') readonly form!: VForm;

  modal = false;
  isLoading = false;
  date: string | null = null;

  jnsKelamin = [
    { state: 'Laki-laki', value: 0 },
    { state: 'Perempuan', value: 1 },
  ];

  companies: CompanyInterface[] = [];
  division: DivisionInterface[] = [];
  subdivision: SubdivisionInterface[] = [];
  department: DepartmentInterface[] = [];

  status = [
    { state: 'Aktif', value: true },
    { state: 'Non Aktif', value: false },
  ];

  bloodItems = [
    { state: 'A', value: 'A' },
    { state: 'B', value: 'B' },
    { state: 'O', value: 'O' },
    { state: 'AB', value: 'AB' },
  ];

  data: UserInterface = {
    id: null,
    active: true,
    name: '',
    email: '',
    avatarUrl: null,
    address: '',
    phone: '',
    divisionId: '',
    identityNumber: null,
    dateOfBirth: null,
    placeOfBirth: '',
    blood: '',
    sex: 0,
    role: 'USER',
    companyId: '',
    subdivisionId: '',
    departmentId: '',
    password: null,
  };

  service: UserServices = new UserServices();

  @MainModule.Action
  public updateTitle!: (newTitle: string) => void;

  mounted(): void {
    this.updateTitle('Update Karyawan');

    this.service.findUser(get(this.$route, 'params.id', 0)).then((res) => {
      const {
        data: { data },
      } = res;
      const getDate = new Date(data.dateOfBirth?.toString() ?? '');
      this.date = `${getDate.getUTCFullYear()}-${
        getDate.getUTCMonth() + 1
      }-${getDate.getUTCDate()}`;
      this.data = data;

      this.service.getDivision(data.companyId).then((res) => {
        const {
          data: { data },
        } = res;
        this.division = data;
      });

      this.service.getSubdivision(data.divisionId).then((res) => {
        const {
          data: { data },
        } = res;
        this.subdivision = data;
      });

      this.service.getDepartment(data.subdivisionId).then((res) => {
        const {
          data: { data },
        } = res;
        this.department = data;
      });
    });

    this.service.getCompany().then((res) => {
      const {
        data: { data },
      } = res;
      this.companies = data;
    });
  }

  async onCreate(): Promise<void> {
    if (this.form.validate()) {
      this.isLoading = true;
      try {
        this.data.dateOfBirth = new Date(this.date ?? '');
        const {
          data: { data: response },
        } = await this.service.updateUser(this.data);
        this.$toast.success(`Berhasil update ${response.name}`);
        this.$router.push({ name: 'Users' });
      } catch (error) {
        if (axios.isAxiosError(error)) {
          const message: string = get(error.response, 'data.message', '');
          this.$toast.error(message);
        }
      }
      this.isLoading = false;
    }
  }

  async onChangeCompany(e: string): Promise<void> {
    try {
      const {
        data: { data: response },
      } = await this.service.getDivision(e);
      this.division = response;
      this.subdivision = [];
      this.department = [];
    } catch (error) {
      if (axios.isAxiosError(error)) {
        const message: string = get(error.response, 'data.message', '');
        this.$toast.error(message);
      }
    }
  }

  async onChangeDivision(e: string): Promise<void> {
    try {
      const {
        data: { data: response },
      } = await this.service.getSubdivision(e);
      this.subdivision = response;
      this.department = [];
    } catch (error) {
      if (axios.isAxiosError(error)) {
        const message: string = get(error.response, 'data.message', '');
        this.$toast.error(message);
      }
    }
  }

  async onChangeSubdivision(e: string): Promise<void> {
    try {
      const {
        data: { data: response },
      } = await this.service.getDepartment(e);
      this.department = response;
    } catch (error) {
      if (axios.isAxiosError(error)) {
        const message: string = get(error.response, 'data.message', '');
        this.$toast.error(message);
      }
    }
  }

  isNumber(evt: KeyboardEvent): boolean {
    const charCode: number = evt.charCode;
    if (charCode > 31 && (charCode < 48 || charCode > 57)) {
      evt.preventDefault();
      return false;
    }
    return true;
  }
}
